import { Component, Element, Host, Prop, h } from '@stencil/core';
import EmblaCarousel, { EmblaCarouselType, EmblaOptionsType } from 'embla-carousel';
import { WheelGesturesPlugin } from 'embla-carousel-wheel-gestures';
import { ICON_BUTTON_SIZES, ICON_BUTTON_VARIANTS } from '../icon-button/exports';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot button - Slot for button in the top right corner
 * @slot slides - Slot for carousel slides
 * Carousel making use of the Embla Carousel library, check out https://www.embla-carousel.com/ for more information.
 */
@Component({
  tag: 'mdx-carousel',
  styleUrl: 'carousel.scss',
  shadow: true,
})
export class Carousel {
  @Element()
  private element: HTMLMdxCarouselElement;

  @Prop()
  carouselTitle = '';

  @Prop()
  loop = false;

  @Prop()
  dragFree = false;

  @Prop()
  prevImageAriaLabel!: string;

  @Prop()
  nextImageAriaLabel!: string;

  private scrollProgress = 0;
  private hasButtonSlot: boolean;

  private addSliderChangeHandler = (emblaAPI: EmblaCarouselType, slider: HTMLInputElement): (() => void) => {
    const onInput = () => {
      const calc = Number(slider.value) * emblaAPI.internalEngine().limit.length * -0.01;
      emblaAPI.internalEngine().scrollBody.useDuration(0);
      emblaAPI.internalEngine().animation.start();
      emblaAPI.internalEngine().target.set(calc);
    };

    slider.addEventListener('input', onInput, false);

    return () => {
      slider.removeEventListener('input', onInput, false);
    };
  };

  private addPrevNextBtnsClickHandlers = (
    emblaApi: EmblaCarouselType,
    prevBtn: HTMLElement,
    nextBtn: HTMLElement,
  ): (() => void) => {
    const scrollPrev = () => {
      for (let i = emblaApi.scrollSnapList().length - 1; i >= 0; i--) {
        if (emblaApi.scrollProgress() > emblaApi.scrollSnapList()[i] + 0.01) {
          if (emblaApi.selectedScrollSnap() === i) {
            emblaApi.scrollPrev();
            return;
          }
          emblaApi.scrollTo(i);
          return;
        }
      }
      if (this.loop) emblaApi.scrollPrev();
    };
    const scrollNext = () => {
      for (let i = 0; i < emblaApi.scrollSnapList().length; i++) {
        if (emblaApi.scrollProgress() < emblaApi.scrollSnapList()[i] - 0.01) {
          if (emblaApi.selectedScrollSnap() === i) {
            emblaApi.scrollNext();
            return;
          }
          emblaApi.scrollTo(i);
          return;
        }
      }
      if (this.loop) emblaApi.scrollNext();
    };

    prevBtn.addEventListener('click', scrollPrev, false);
    nextBtn.addEventListener('click', scrollNext, false);

    return () => {
      prevBtn.removeEventListener('click', scrollPrev, false);
      nextBtn.removeEventListener('click', scrollNext, false);
    };
  };

  componentWillLoad() {
    this.hasButtonSlot = !!this.element.querySelector('[slot="button"]');
  }

  componentDidLoad() {
    if (!this.element.shadowRoot) return;

    const emblaNode = this.element.shadowRoot.querySelector('.embla') as HTMLElement;
    if (!emblaNode) return;

    const slot = this.element.shadowRoot.querySelector('slot[name="slides"]') as HTMLSlotElement;
    const slides = slot.assignedElements();

    const container = this.element.shadowRoot.querySelector('.embla__container') as HTMLElement;
    slides.forEach((slide) => {
      container.appendChild(slide);
    });

    slot.remove();

    const options: EmblaOptionsType = { loop: this.loop, dragFree: this.dragFree };
    const emblaApi = EmblaCarousel(emblaNode, options, [WheelGesturesPlugin()]);
    const prevBtn = this.element.shadowRoot.querySelector('.carousel__slide-bar__button--prev') as HTMLElement;
    const nextBtn = this.element.shadowRoot.querySelector('.carousel__slide-bar__button--next') as HTMLElement;
    const slider = this.element.shadowRoot.querySelector('.carousel__slider') as HTMLInputElement;

    const removeButtonEventListeners = this.addPrevNextBtnsClickHandlers(emblaApi, prevBtn, nextBtn);
    const removeSliderEventListeners = this.addSliderChangeHandler(emblaApi, slider);

    const toggleButtonsDisabled = () => {
      if (this.loop || this.scrollProgress > 0.01) prevBtn.removeAttribute('disabled');
      else prevBtn.setAttribute('disabled', 'true');

      if (this.loop || this.scrollProgress < 0.99) nextBtn.removeAttribute('disabled');
      else nextBtn.setAttribute('disabled', 'true');
    };

    const sliderWidth = 100 / emblaApi.scrollSnapList().length;
    slider.style.setProperty('--thumbWidth', sliderWidth + '%');

    emblaApi.on('init', toggleButtonsDisabled);
    emblaApi.on('reInit', toggleButtonsDisabled);
    emblaApi.on('scroll', () => {
      this.scrollProgress = emblaApi.scrollProgress();
      slider.value = this.scrollProgress * 100 + '';
      toggleButtonsDisabled();
    });
    emblaApi.on('resize', () => {
      this.scrollProgress = emblaApi.scrollProgress();
      slider.value = this.scrollProgress * 100 + '';
      const sliderWidth = 100 / emblaApi.scrollSnapList().length;
      slider.style.setProperty('--thumbWidth', sliderWidth + '%');
      toggleButtonsDisabled();
    });
    emblaApi.on('destroy', removeButtonEventListeners);
    emblaApi.on('destroy', removeSliderEventListeners);
  }

  render() {
    return (
      <Host>
        {this.carouselTitle !== '' || this.hasButtonSlot ? (
          <div class="carousel__top">
            <h3 class="carousel__top__title">{this.carouselTitle}</h3>
            <slot name="button" />
          </div>
        ) : null}

        <div class="embla">
          <div class="embla__container">
            <slot name="slides" />
          </div>
        </div>

        <div class="carousel__slide-bar">
          <mdx-icon-button
            class="carousel__slide-bar__button--prev"
            size={ICON_BUTTON_SIZES.MD}
            variant={ICON_BUTTON_VARIANTS.TERTIARY}
            rounded={true}
            ariaLabel={this.prevImageAriaLabel}
            disabled={!this.loop && this.scrollProgress <= 0}
          >
            <mdx-icon-chevron-left size={ICON_SIZES.SM} />
          </mdx-icon-button>
          <input class="carousel__slider" type="range" min="0" max="100" value="0" step="0.1" aria-label="carousel slider" />
          <mdx-icon-button
            class="carousel__slide-bar__button--next"
            size={ICON_BUTTON_SIZES.MD}
            rounded={true}
            variant={ICON_BUTTON_VARIANTS.TERTIARY}
            ariaLabel={this.nextImageAriaLabel}
            disabled={!this.loop && this.scrollProgress >= 1}
          >
            <mdx-icon-chevron-right size={ICON_SIZES.SM} />
          </mdx-icon-button>
        </div>
      </Host>
    );
  }
}
