:host .carousel__top {
  margin-bottom: var(--mdx-comp-carousel-spacing-carousel-box-container-gap-header-content);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  & > h3 {
    font: var(--mdx-comp-carousel-carousel-box-font-header-title-default);
    letter-spacing: var(--mdx-comp-carousel-carousel-box-font-header-title-default-letter-spacing);
    text-decoration: var(--mdx-comp-carousel-carousel-box-font-header-title-default-text-decoration);
    margin: 0;
  }
}

:host .embla {
  width: var(--mdx-comp-carousel-sizing-carousel-box-width);
  overflow: hidden;

  &__container {
    display: flex;

    & > * {
      flex: 0 0 auto;
      min-width: 0;
      height: auto;
      margin-right: var(--mdx-comp-carousel-spacing-carousel-box-content-gap-horizontal-cards);
    }
  }
}

:host .carousel__slide-bar {
  margin-top: var(--mdx-comp-carousel-spacing-carousel-box-content-gap-cards-slidebar);
  width: 100%;
  height: var(--mdx-comp-carousel-sizing-scrollbar-container-height);
  display: flex;
  flex-direction: row;
  gap: var(--mdx-comp-carousel-spacing-scrollbar-container-gap);
  align-items: center;

  @media screen and (max-width: 768px) {
    & > mdx-icon-button {
      display: none;
    }
  }
}

:host .carousel__slider {
  --thumbWidth: 48px;
  position: relative;
  appearance: none;
  margin: 0;
  width: var(--mdx-comp-carousel-sizing-scrollbar-container-width);
  height: var(--mdx-comp-carousel-sizing-scrollbar-track-height);
  background-clip: content-box;
  padding-top: calc(
    (var(--mdx-comp-carousel-sizing-scrollbar-container-height) - var(--mdx-comp-carousel-sizing-scrollbar-track-height)) / 2
  );
  padding-bottom: calc(
    (var(--mdx-comp-carousel-sizing-scrollbar-container-height) - var(--mdx-comp-carousel-sizing-scrollbar-track-height)) / 2
  );
  outline: none;
  z-index: 0;

  &:hover {
    &::-webkit-slider-thumb {
      background: var(--mdx-comp-carousel-scrollbar-thumb-background-color-hover);
    }
    &::-moz-range-thumb {
      background: var(--mdx-comp-carousel-scrollbar-thumb-background-color-hover);
    }
  }

  &::-webkit-slider-thumb {
    appearance: none;
    min-width: 48px;
    width: var(--thumbWidth);
    height: var(--mdx-comp-carousel-sizing-scrollbar-thumb-height);
    border-radius: var(--mdx-comp-carousel-border-radius-scrollbar-thumb-default);
    background: var(--mdx-comp-carousel-scrollbar-thumb-background-color-default);
    cursor: pointer;
    z-index: 1;
  }

  &::-moz-range-thumb {
    appearance: none;
    min-width: 48px;
    width: var(--thumbWidth);
    height: var(--mdx-comp-carousel-sizing-scrollbar-thumb-height);
    border-radius: var(--mdx-comp-carousel-border-radius-scrollbar-thumb-default);
    background: var(--mdx-comp-carousel-scrollbar-thumb-background-color-default);
    cursor: pointer;
    z-index: 1;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: var(--mdx-comp-carousel-scrollbar-track-background-color-default);
    margin-top: calc(
      (var(--mdx-comp-carousel-sizing-scrollbar-container-height) - var(--mdx-comp-carousel-sizing-scrollbar-track-height)) /
        2
    );
    margin-bottom: calc(
      (var(--mdx-comp-carousel-sizing-scrollbar-container-height) - var(--mdx-comp-carousel-sizing-scrollbar-track-height)) /
        2
    );
    border-radius: var(--mdx-comp-carousel-border-radius-scrollbar-track-default);
    z-index: -1;
  }
}
